// ============================================
// GoHighLevel Import Execution API Route
// ============================================

import { NextRequest, NextResponse } from 'next/server'
import { cookies } from 'next/headers'

interface ImportOptions {
  importContacts: boolean
  importPipelines: boolean
  importOpportunities: boolean
  importCalendars: boolean
  importAppointments: boolean
  importConversations: boolean
  importTasks: boolean
  importNotes: boolean
  importTags: boolean
  importCustomFields: boolean
  mergeExistingContacts: boolean
  skipDuplicates: boolean
}

interface ImportResult {
  type: string
  imported: number
  skipped: number
  errors: number
  duration: number
}

/**
 * POST /api/v1/import/ghl/run
 * Executes the import from GoHighLevel
 */
export async function POST(request: NextRequest) {
  try {
    const body = await request.json()
    const { 
      subAccountId, 
      options, 
      demoMode = false 
    }: { 
      subAccountId: string
      options: ImportOptions
      demoMode?: boolean 
    } = body

    if (!subAccountId) {
      return NextResponse.json(
        { success: false, error: 'subAccountId is required' },
        { status: 400 }
      )
    }

    // Check for GHL connection
    const cookieStore = await cookies()
    const connectionData = cookieStore.get('ghl_connection')?.value

    if (!connectionData && !demoMode) {
      return NextResponse.json(
        { 
          success: false, 
          error: 'Not connected to GoHighLevel. Please connect first.',
          requiresConnection: true,
        },
        { status: 401 }
      )
    }

    const startTime = Date.now()

    // Demo mode - simulate import process
    if (demoMode) {
      // Simulate import delay
      await new Promise(resolve => setTimeout(resolve, 500))

      const results: Record<string, ImportResult> = {}

      if (options.importTags) {
        results.tags = { type: 'tags', imported: 48, skipped: 0, errors: 0, duration: 1200 }
      }
      if (options.importCustomFields) {
        results.customFields = { type: 'customFields', imported: 24, skipped: 0, errors: 0, duration: 800 }
      }
      if (options.importContacts) {
        results.contacts = { type: 'contacts', imported: 2845, skipped: 2, errors: 0, duration: 28000 }
      }
      if (options.importPipelines) {
        results.pipelines = { type: 'pipelines', imported: 5, skipped: 0, errors: 0, duration: 500 }
      }
      if (options.importOpportunities) {
        results.opportunities = { type: 'opportunities', imported: 420, skipped: 3, errors: 0, duration: 8500 }
      }
      if (options.importCalendars) {
        results.calendars = { type: 'calendars', imported: 3, skipped: 0, errors: 0, duration: 300 }
      }
      if (options.importAppointments) {
        results.appointments = { type: 'appointments', imported: 312, skipped: 0, errors: 0, duration: 6200 }
      }
      if (options.importConversations) {
        results.conversations = { type: 'conversations', imported: 12450, skipped: 3, errors: 0, duration: 45000 }
      }
      if (options.importTasks) {
        results.tasks = { type: 'tasks', imported: 155, skipped: 1, errors: 0, duration: 3100 }
      }
      if (options.importNotes) {
        results.notes = { type: 'notes', imported: 2340, skipped: 1, errors: 0, duration: 9500 }
      }

      const totalDuration = Date.now() - startTime

      return NextResponse.json({
        success: true,
        data: {
          results,
          summary: {
            totalImported: Object.values(results).reduce((sum, r) => sum + r.imported, 0),
            totalSkipped: Object.values(results).reduce((sum, r) => sum + r.skipped, 0),
            totalErrors: Object.values(results).reduce((sum, r) => sum + r.errors, 0),
            duration: totalDuration,
          },
          completedAt: new Date().toISOString(),
          source: 'GoHighLevel (Demo Mode)',
        },
      })
    }

    // Real mode - use GHLImporter
    const connection = JSON.parse(connectionData!)

    // In production, this would use the GHLImporter class
    // const importer = new GHLImporter(connection.accessToken, connection.locationId)
    // const result = await importer.runImport(options)

    // For now, return a placeholder response
    return NextResponse.json({
      success: true,
      data: {
        results: {},
        summary: {
          totalImported: 0,
          totalSkipped: 0,
          totalErrors: 0,
          duration: Date.now() - startTime,
        },
        completedAt: new Date().toISOString(),
        source: 'GoHighLevel',
        message: 'Real import functionality requires GHL API credentials',
      },
    })
  } catch (error) {
    console.error('GHL Import Error:', error)
    return NextResponse.json(
      { 
        success: false, 
        error: error instanceof Error ? error.message : 'Import failed' 
      },
      { status: 500 }
    )
  }
}

/**
 * GET /api/v1/import/ghl/run
 * Returns the status of an ongoing import
 */
export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const importId = searchParams.get('importId')
    const subAccountId = searchParams.get('subAccountId')

    if (!subAccountId) {
      return NextResponse.json(
        { success: false, error: 'subAccountId is required' },
        { status: 400 }
      )
    }

    // In production, this would check the status of an ongoing import
    // For now, return completed status
    return NextResponse.json({
      success: true,
      data: {
        status: 'completed',
        progress: 100,
        currentStep: 'Import completed',
        importId,
      },
    })
  } catch (error) {
    console.error('GHL Import Status Error:', error)
    return NextResponse.json(
      { 
        success: false, 
        error: error instanceof Error ? error.message : 'Failed to get import status' 
      },
      { status: 500 }
    )
  }
}
